@import url('../../../assets/feather/style.min.css');
@import url('../../../assets/fontawesome/css/all.css');
@import url('../../../assets/line-awesome/css/line-awesome.min.css');
:host {
  display: block;
  margin-bottom: 0.5rem;
}
.currency {
  display: block;
  position: absolute;
  margin: 0;
  padding: 0;
  height: auto;
  left: 10px;
}
.rate-input {
  font-size: 14px;
  line-height: 0;
  padding: 0;
  height: 0;
}
.rate-input-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  flex: 1;

}

@media  only screen and (min-width: 1200px) { 
  .rateplan-name-container {
    width: 40%;
  }
  .rateplan-container{
    width: 40%;
  }
  
}
@media only screen and  (min-width: 991px)  { 
  
  .max-w-300{
    max-width: 200px;
  } 
  .rate-input-container{
    max-width: 100px;
  }
}
@media only screen and  (min-width: 991px) and (max-width: 1300px) { 
  .rateplan-name-container {
    width: 35%;
  }
}
@media only screen and   (max-width: 768px) { 
  .booking-btn{
    width: 100%;
  }
}

.total-nights-container{
  width: max-content;
}
.rateInputBorder{
  border-top-left-radius: 3px !important;
  border-bottom-left-radius: 3px !important;
}
.nightBorder{
  border-left-width: 0;
  border-top-right-radius: 3px !important;
  border-bottom-right-radius: 3px !important;
}